@import url("//netdna.bootstrapcdn.com/font-awesome/4.0.3/css/font-awesome.css");

.home,
.contact,
.about {
  background: -webkit-gradient(linear, left top, left bottom, from(#85C9B1), to(#fcf8cf));
  background: -webkit-linear-gradient(#85C9B1, #fcf8cf);
  background: -o-linear-gradient(#85C9B1, #fcf8cf);
  background: linear-gradient(#85C9B1, #fcf8cf);
  background-size: contain;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 100vh;
  min-width: 100vw;
  z-index: -1;
}

.projects {
  background-color: #85C9B1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 100vh;
  min-width: 100vw;
  background-repeat: repeat;
}

/* TODO: change this method */
html {
  background-color: #85C9B1;
}


body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.about-header {
  font-size: 65px;
  left: 75px;
  position: absolute;
  z-index: 1;
}

.about-me-text {
  position: absolute;
  left: 50px;
  top: 150px;
  width: 750px;
  background-color: #eff2f7;
  font-size: 18px;
  text-align: left;
  padding: 20px;
  border-radius: 5px;
}

.btn-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

.contact {
  height: 500px;
  width: 1000px;
  -ms-flex-direction: row;
      flex-direction: row;
  /* position: relative; */
}

.contact-container {
  margin: 0px 100px;
}

.contact-types {
  color: #56a095;
  font-size: 18px;
}

.contact-text {
  font-size: 78px;
  margin-bottom: 0px;
  margin-top: 0px;
}

.icon-container {
  text-align: right;
}

.contact-types{
  margin-bottom: 0px;
}

.contact h4 {
  font-size: 25px;
}

.contact h3 {
  margin-top: 10px;
  font-size: 20px;
}

.contact-info-container {
  text-align: left;
}

.desktop-image {
  width: 650px;
  position: absolute;
  top: 130px;
  right: 35px;
  opacity: 0.8;
}

.front-btn {
  border: 2px solid black;
  background-color: white;
  color: black;
  width: 150px;
  height: 60px;
  margin: 30px 50px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
}
.front-btn:hover {
  background: #e7e7e7;
}

.front-page-profile-image {
  border-radius: 50%;
  width: 200px;
}

.gray-border {
  border-style: solid;
  border-width: 2px;
  border-color: #c7c9cc;
}

.inner-white-container {
  background-color: #eff2f7;
  width: 80vw;
  height: 85vh;
  position: relative;
  border-radius: 7px;
}

.projects-inner-white-container {
  background-color: #eff2f7;
  width: 80vw;
  height: auto;
  margin-top: 55px;
  margin-bottom: 55px;
  position: relative;
  border-radius: 7px;
}

.large-name {
  font-size: 50px
}

.name-picture-container {
  padding: 0;
  margin: 30px 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

.name-picture-item {
  padding: 20px
}

.navbar {
  margin: 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  position: absolute;
  top: 10px;
  right: 75px;
}

.navbar-link:hover {
  color: #eff2f7;
}

.navbar-link {
  color: black;
  text-decoration: none;
  margin: 5px 15px 5px 0px;
}

.navbar-link:visited {
  text-decoration: none;
  color:#000;
}

.project-container {
  margin: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  background-color: #fcfeff
}

.project-header {
  background-color: #b8c1d1;
  height: 30px;
}

.project-title {
  padding: 0px;
  margin: 0px 10px;
  font-size: 18px;
  text-align: left;
}

.proj-desc-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin: 0px 20px
}

.project-description {
  text-align: left;
  margin-left: 20px;
}

.proj-images-container {
  margin: 20px;
}

.proj-image {
  height: 150px;
  margin: 0px 20px
}

.proj-section-title {
  font-size: 20px;
}

.proj-links-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  padding: 0px;
  -ms-flex-pack: center;
      justify-content: center
}

.small-icon {
  font-size: 50px;
  margin: 0px 10px;
  text-decoration: none;
  color: black
}

.project-links {
  text-align: center;
  display: inline;
  margin: 0px 10px 20px 10px;
}




